import * as yup from "yup";

const SignupSchema = yup.object({
  firstName: yup
    .string()
    .min(3, "  3 ")
    .matches(/^([-]{1,23}|[a-z]{1,23})$/, " ")
    .required(" "),
  lastName: yup
    .string()
    .min(3, "  3 ")
    .matches(/^([-]{1,23}|[a-z]{1,23})$/, " ")
    .required(" "),
  email: yup.string().email(" email").required(" "),
  password: yup
    .string()
    .min(8, "  8 ")
    .required(" "),
  confirmPassword: yup
    .string()
    .oneOf([yup.ref("password")], "  ")
    .required(" "),
});

export default SignupSchema;
